@use '../base/mixins' as m;

.barra {
    display: flex;
    flex-direction: column;

    @include m.tablet { 
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    .logo {
        width: 50%; /* tome el 50% de la pantalla */
        margin: 0 auto;

        @include m.tablet { 
            width: 20rem;
            margin: 0;
    }
    }
}
